:root {
  --bg-card: #474747;
  --bg-body: #68dbc8;
  --bg-card-back: #e02498;
  --color-white: #fff;
  --color-title-1: #00e6e6;
  --color-title-2: #01cccc;
  --color-title-3: #00bdbd;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: var(--bg-body);
  height: 100vh;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
  font-family: Roboto, sans-serif;
}

.title {
  color: var(--color-white);
  text-shadow: -5px 5px 0px var(--color-title-1), -10px 10px 0px var(--color-title-2), -15px 15px 0px var(--color-title-3);
  justify-content: center;
  font-size: 4rem;
  display: flex;
}

.content {
  height: 100vh;
  flex-direction: column;
  justify-content: space-evenly;
  gap: 1rem;
  padding: 1rem 0;
  display: flex;
}

.cards {
  grid-template-columns: repeat(4, 1fr);
  gap: 1rem;
  display: grid;
}

.card {
  background-color: var(--bg-card);
  cursor: pointer;
  height: 10rem;
  transform-style: preserve-3d;
  border-radius: 1rem;
  justify-content: center;
  align-items: center;
  font-size: 5rem;
  transition: transform 1s;
  display: flex;
}

.attempts {
  background-color: var(--bg-card);
  color: var(--color-white);
  border-radius: 1rem;
  align-self: center;
  padding: 1rem;
}

.front, .back {
  backface-visibility: hidden;
  height: 100%;
  -webkit-user-select: none;
  user-select: none;
  width: 100%;
  border-radius: 1rem;
  place-content: center;
  font-size: 8rem;
  display: grid;
  position: absolute;
}

.back {
  background: linear-gradient(var(--bg-card-back), var(--color-white) );
  transform: rotateY(180deg);
}

.flipped {
  cursor: default;
  transition: transform .5s;
  transform: rotateY(180deg);
}

.modal-overlay {
  height: 100vh;
  width: 100vw;
  z-index: 10;
  background-color: #0009;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-container {
  background-color: var(--bg-card);
  color: var(--color-white);
  height: 200px;
  border-radius: 1rem;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  display: flex;
}

.modal-title {
  text-align: center;
}

.button {
  border: 1px solid var(--color-white);
  background-color: var(--bg-card);
  color: var(--color-white);
  cursor: pointer;
  border-radius: 1rem;
  padding: .5rem 1rem;
  font-size: 1rem;
}

.button:hover {
  border-color: var(--bg-card-back);
}

